<template>
    <!-- Vehicle of Interest -->
    <div class="vehicle-interest">
        <!-- Vehicle-tab -->
        <ul class="nav nav-tabs" id="myTab" role="tablist">
            <li class="nav-item ">
            <a class="nav-link active" id="Vehicle-tab" data-toggle="tab" href="#Vehicle" role="tab" aria-controls="Vehicle" aria-selected="true">Vehicle of Interest</a>
            </li>
            <li class="nav-item ">
            <a class="nav-link" id="Trades-tab" data-toggle="tab" href="#Trades" role="tab" aria-controls="Trades" aria-selected="false">Trades</a>
            </li>
        </ul>
        <div class="tab-content" id="myTabContent">
            <div class="tab-pane fade show active" id="Vehicle" role="tabpanel" aria-labelledby="Vehicle-tab">
                <!-- detais-title -->
                <div class="detais-search">
                    <div class="form-group Vehicle">
                        <input type="search" class="form-control" id="inputAddress23" placeholder="Search Inventory">
                        <img class="img-fluid Vehi-img" src="../../assets/img/search.svg" alt="">
                    </div>
                </div>
                <carousel :per-page="1" :mouse-drag="true">
                    <slide v-for="dtl in voi" :key="dtl.ld_id">
                        <div class="vehicle-img">
                            <img class="img-fluid" src="../../assets/img/Rectangle 99.svg" alt="">
                        </div>
                        <div class="vehicle-table">
                            <h4>{{dtl.ld_voi_year}} {{dtl.ld_voi_make}} </h4>
                            <table class="table">
                            <tbody>
                                <tr>
                                    <td>Stock#</td>
                                    <td>{{dtl.ld_voi_stockno}}</td>
                                </tr>
                                <tr>
                                    <td>Mile</td>
                                    <td>{{dtl.lti_vehicle_mileage}}</td>
                                </tr>
                                <tr>
                                    <td>VIN</td>
                                    <td>{{dtl.ld_voi_vin}}</td>
                                </tr>
                            </tbody>
                            </table>
                        </div>
                    </slide>
                    <!-- detais-title -->
                </carousel>    
            </div>
            <div class="tab-pane fade" id="Trades" role="tabpanel" aria-labelledby="Trades-tab">
                <!-- detais-title -->
                <div class="detais-search">
                    <a href="#" class="trade-btn" data-toggle="modal" data-target="#addTradeModal"><img class="img-fluid" src="../../assets/img/23. Close.svg" alt=""> Add</a>
                </div>
                <carousel :per-page="1" :mouse-drag="true">
                    <slide v-for="trade in trades" :key="trade.lti_id">
                        <div class="vehicle-img">
                            <img class="img-fluid" src="../../assets/img/Rectangle 99.svg" alt="">
                        </div>
                        <div class="vehicle-table">
                            <h4>{{trade.lti_vehicle_year}} {{trade.lti_vehicle_make}} </h4>
                            <table class="table">
                            <tbody>
                                <tr>
                                    <td>Stock#</td>
                                    <td>{{trade.lti_vehicle_stockno}}</td>
                                </tr>
                                <tr>
                                    <td>Mile</td>
                                    <td>{{trade.lti_vehicle_mileage}}</td>
                                </tr>
                                <tr>
                                    <td>Color</td>
                                    <td>{{trade.lti_vehicle_color}}</td>
                                </tr>
                            </tbody>
                            </table>
                        </div>
                    </slide>
                    <!-- detais-title -->
                </carousel>
            </div>
        </div>
        <!-- Vehicle-tab -->
    
        <!-- Similir vehicle -->
        <div class="similir-vehicle">
            <div class="detais-title">
            <h3>Similir vehicle of inventory</h3>
            </div>
            <div class="vehicle-img">
            <a href="#"><img class="img-fluid" src="../../assets/img/Rectangle 100.svg" alt=""></a>
            </div>
            
        </div>
        <!-- Similir vehicle -->

        <!-- Add Trade Modal -->
        <div class="modal fade" id="addTradeModal" tabindex="-1" role="dialog" aria-labelledby="Add Trade" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Add Trade</h5>
                        <a class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true"><img class="img-fluid" src="../../assets/img/23. Closex.svg" alt=""></span>
                        </a>
                    </div>
                    <form v-on:submit.prevent = "addTrade">
                        <div class="modal-body">
                            <div class="add-member add-trades">
                                <div class="form-row">
                                    <div class="form-group col-md-4">
                                        <label for="t_years">Years <span>*</span></label>
                                        <input type="text" class="form-control" id="t_years" placeholder="2010" v-model="trade_.years">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>Make <span>*</span></label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.make">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>Model</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.model">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>Trim / Style</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.style">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>Color</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.color">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>VIN</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.vin">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>Mileage</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.mileage">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>ACV</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.acv">
                                    </div>
                                    <div class="form-group col-md-4">
                                    <label>Allowance</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.allowance">
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label>Notes</label>
                                    <textarea class="form-control"  rows="2" placeholder="Type here..." v-model="trade_.notes"></textarea>
                                </div>
                                <div class="form-group">
                                    <label>Payoff</label>
                                    <input type="text" class="form-control" placeholder="Text" v-model="trade_.payoff">
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <a href="#" data-dismiss="modal" aria-label="Close" class="add-btn">Cancel</a>
                            <button type="submit" class="rem-btn">Save trade</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>

        <!-- Add Trade Modal -->

    </div>
    <!-- Vehicle of Interest -->
</template>

<script>

import axios from 'axios'
import VueDaval from 'vue-daval'
import $ from 'jquery'
import { Carousel, Slide } from 'vue-carousel';
export default {
    name: 'VehicleOfInterest',
    data(){
		return{
            trade_: {years:'',make:''},
            trades:{},
            voi:{}
        }
    },
    components: {
        Carousel,Slide
    },
    mixins: [ VueDaval ],
    vdRules: {
        trade_:  {
            years:{required: true},make:{required: true},
            //tMin:{required: true}, tDtype: {required: true},
            //desc:{required: true}
        }
    },
    created(){

        this.getTrades()
        this.getVOI()
    },
    methods:{
		getTrades(){
			axios
			.get('tradeins/'+this.$route.params.id)
			.then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.trades = data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getVOI(){
			axios
			.get('voi/'+this.$route.params.id)
			.then( res => {
                if (res.data.success) {
                    const data = res.data
                    this.voi = data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
        addTrade(){
            console.log(this.trade_);
            this.trade_.lead_id = this.$route.params.id
            this.$vd.trade_.$validate().then(() => {
                axios
                .post('add_trade',this.trade_,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        $("#addTradeModal").modal('hide')
                        this.trade_ = {}
                        this.getTrades()
                    }
                })
                .catch(err =>{
                    console.log(err)
                });
            }).catch(err => {
                console.log(err)
            });
        }

    }
}
</script>